.container {
	min-width: 320px;
	max-width: 375px;
	top: 61px;
}

.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: #1616161e;
	z-index: 100;
}

.cont {
	background-color: var(--color-modal-time);
	max-width: 335px;
	max-height: 440px;
	border: 1px solid var(--color-borer-modal);
	border-radius: 8px;
	box-shadow: 0 4px 16px hsla(0, 0%, 7%, 0.1);
	padding: 24px;
	margin: 0 auto;
	position: relative;
}

.wrap {
	position: absolute;
	top: 14px;
	right: 14px;
	cursor: pointer;
}

.closeBtn {
	width: 18px;
	height: 18px;
}

.txt {
	color: var(--color-text-base);
	font-family: "Poppins", sans-serif;
	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	margin: 0;
}

.avatarContainer {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	border-radius: 8px;
	margin-top: 24px;
}

.closeBtn {
	color: var(--color-text-base);
	background-color: transparent;
	border: none;
	cursor: pointer;
	padding: 0;
}

.plusBtn {
	border: none;
	background-color: var(--bg-add-user);
	border-radius: 4px;
	width: 24px;
	height: 24px;
	position: absolute;
	top: 60px;
	cursor: pointer;
	color: #161616;
	transition: all 0.3s ease-in-out;
	display: flex;
	justify-content: center;
	align-items: center;
}

.plusBtn:hover {
	background-color: var(--color-green-dark);
}

.formInput {
	padding: 14px 18px;
	width: 100%;
	height: 49px;
	border-radius: 8px;
	border: 1px solid var(--color-green-border);
	background-color: inherit;
	color: var(--color-text-base);
	font-family: "Poppins", sans-serif;
	font-size: 14px;
	font-weight: 400;
	outline: none;
	box-shadow: none;
	position: relative;
}

.formInput:focus {
	border: 1px solid var(--color-green);
}

.formGroup {
	position: relative;
}

.error {
	height: 0px;
	color: red;
	font-size: 12px;
	text-align: left;
	position: absolute;
	bottom: 0;
	left: 0;
}

.forma {
	display: flex;
	flex-direction: column;
	gap: 14px;
	margin-top: 32px;
}

.btn {
	font-family: inherit;
	font-size: 14px;
	font-weight: 600;
	font-style: normal;
	align-items: center;
	background-color: var(--color-green);
	border: none;
	border-radius: 8px;
	color: var(--color-btn-text);
	cursor: pointer;
	gap: 8px;
	line-height: 21px;
	padding-bottom: 11px;
	padding-top: 10px;
	width: 100%;
	margin-top: 14px;
	transition: all 0.3s ease-in-out;
}

.btn:hover {
	background-color: var(--color-green-dark);
}


.eye {
	background-color: transparent;
	border: none;
	color: var(--white);
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 18px;
}

.avatar {
	display: block;
	cursor: pointer;
	border-radius: 5px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	width: 68px;
	height: 68px;
	background-color: transparent;
}

@media screen and (min-width: 320px) and (max-width: 375px) {
	.cont {
		max-width: 320px;
		padding: 16px;
	}

	.formInput {
		padding: 12px 16px;
		font-size: 14px;
	}

	.txt {
		font-size: 18px;
	}

	.btn {
		font-size: 14px;
		padding: 8px;
	}
}

@media screen and (min-width: 375px) {
	.wrap {
		right: 14px;
	}
}

@media screen and (min-width: 768px) {
	.cont {
		position: fixed;
		right: 20px;
		top: 60px;
		margin-top: 5px;
		margin-bottom: 5px;
		max-height: 440px;
		width: 400px;
	}

	.container {
		max-width: 768px;
	}

	.wrap {
		right: 14px;
	}
}

@media screen and (min-width: 768px) and (max-width: 1180px) {
	.avatarContainer {
		margin-top: 14px;
	}
}

@media screen and (min-width: 1180px) {
	.wrap {
		right: 14px;
	}

	.cont {
		top: 60px;
	}
}