.input {
  width: 100%;
  height: 49px;
  border: 1px solid var(--color-green-border);
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 2px;
  background-color: var(--modal-color-transparent);
  color: var(--color-text-base);
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: -0.02em;
  outline: none;
}

.input:focus {
  outline: none;
  border: 1px solid var(--color-green);
}

.listIcons {
  display: flex;
  gap: 8px;
  margin-bottom: 24px;
}

.icon {
  width: 18px;
  height: 18px;
  opacity: 0.5;
  color: var(--newboard-icon);
  cursor: pointer;
  transition: all 0.8s ease-in-out;
}

.icon:hover {
  transform: rotate(360deg);
}

.inputIcon:checked + .labeIcon .icon {
  opacity: 1;
}

.labeIcon .icon {
  opacity: 0.5;
}

.inputIcon {
  display: none;
}

.text {
  margin-top: 24px;
  margin-bottom: 14px;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.02em;
  color: var(--color-text-base);
}

.listBackground {
  width: 100%;
  display: flex;
  gap: 4px;
  margin-bottom: 40px;
  flex-wrap: wrap;
}

.backgroundBox {
  border-radius: 8px;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.emptyPicture {
  width: 28px;
  height: 28px;
  fill: var(--modal-icon);
  color: rgb(56, 55, 55);
}

.iconImg {
  width: 28px;
  height: 28px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.iconImg:hover,
.iconImg:focus,
.iconImg.selected {
  transform: scale(1.08);
  box-shadow: 0px 0px 0px 1px var(--color-text-rgb);
}

.backgroundBox:hover,
.backgroundBox.selected {
  transform: scale(1.08);
  box-shadow: 0px 0px 0px 1px var(--color-text-rgb);
}

.errors {
  position: relative;
  bottom: -6px;
  font-size: 10px;
  color: rgba(255, 0, 0, 0.788);
  left: 18px;
}

.input.error {
  border-color: rgba(255, 0, 0, 0.788);
}

.skeletonBackground {
  width: 28px;
  height: 28px;
  background-color: #e0e0e0;
  border-radius: 8px;
  animation: pulse 1.5s infinite ease-in-out;
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}
